import React, { useState } from "react";
import { AppBar, Toolbar, IconButton, Typography, Switch, Link } from "@mui/material";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import GitHubIcon from "@mui/icons-material/GitHub";
import { Link as RouterLink } from "react-router-dom";

export default function Navbar({ toggleDarkMode }) {
  return (
    <AppBar position="static" color="primary">
      <Toolbar>
        <Typography variant="h6" sx={{ flexGrow: 1 }}>
          My Portfolio
        </Typography>
        <Link component={RouterLink} to="/" color="inherit" sx={{ margin: 1 }}>
          About
        </Link>
        <Link component={RouterLink} to="/blogs" color="inherit" sx={{ margin: 1 }}>
          Blogs
        </Link>
        <Link component={RouterLink} to="/resume" color="inherit" sx={{ margin: 1 }}>
          Resume
        </Link>
        <Link component={RouterLink} to="/contributions" color="inherit" sx={{ margin: 1 }}>
          Contributions
        </Link>
        <Link component={RouterLink} to="/prs" color="inherit" sx={{ margin: 1 }}>
          PRs
        </Link>
        <IconButton href="https://www.linkedin.com/" target="_blank" color="inherit">
          <LinkedInIcon />
        </IconButton>
        <IconButton href="https://github.com/" target="_blank" color="inherit">
          <GitHubIcon />
        </IconButton>
        <Switch onChange={toggleDarkMode} />
      </Toolbar>
    </AppBar>
  );
}
